.container {
    max-width: 80%;
    margin: auto;
}

.flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

footer {
    background-color: #1d2636;
    padding: 50px 0;
    color: #fff;
}

footer .container {
    display: grid;
    grid-template-columns: 6fr 2fr 2fr 1fr;
    grid-gap: 20px;
}

footer img {
    width: 150px;
}

footer h2 {
    font-weight: 500;
}

footer p {
    color: grey;
    margin: 20px 0;
}

footer input {
    background-color: #fff;
    padding: 17px;
    width: 100%;
    border-radius: 5px;
}

footer h3 {
    font-weight: 500;
    margin-bottom: 30px;
}

footer ul {
    display: block;
}

footer ul li {
    display: block;
    color: grey;
    margin-bottom: 20px;
}

@media screen and (max-width: 800px) {
    footer .container {
        grid-template-columns: repeat(2, 1fr);
    }
}
